import React from 'react'

function Buttons({collection}) {

    const EURUSD = () =>{
       collection.setCurrency('EURUSD')
    }

    const JPPUSD = () =>{
        collection.setCurrency('JPYUSD')
    }

    const GDUSD = () =>{
        collection.setCurrency('GBPUSD')
    }
  return (
    <div style={{display:'flex', justifyContent:'center', gap:'5px', padding:'10px'}}>
        <div onClick={EURUSD} style={{background:collection.currency=='EURUSD'?'rgb(47, 106, 255)':'rgb(168, 192, 255)', padding:'10px', fontSize:'12px', borderRadius:'10px', color:'white', cursor:'pointer'}}>EURUSD</div>
        <div onClick={JPPUSD} style={{background:collection.currency=='JPYUSD'?'rgb(47, 106, 255)':'rgb(168, 192, 255)', padding:'10px', fontSize:'12px', borderRadius:'10px', color:'white', cursor:'pointer'}}>JPYUSD</div>
        <div onClick={GDUSD} style={{background:collection.currency=='GBPUSD'?'rgb(47, 106, 255)':'rgb(168, 192, 255)', padding:'10px', fontSize:'12px', borderRadius:'10px', color:'white', cursor:'pointer'}}>GBPUSD</div>
    </div>
  )
}

export default Buttons